import {get,post} from "../config/config"
import axios from "axios";
let baseQqUrl = "/cgi-bin/report_judge/qq/";

//
export const query_admin_user_auth = params => get(`${baseQqUrl}query_admin_user_auth`,params);

//
export const setting_auth_file = (params,config) => post(`${baseQqUrl}modify_user_auth`, params,config);

//
export const query_auth_history = params => get(`${baseQqUrl}query_modify_auth_history`,params);

//
export const get_case_extra_info = params => get(`${baseQqUrl}get_case_extra_info`,params);

//
export const query_court_case_info = params => get(`${baseQqUrl}query_court_case_info`,params);

//
export const query_user_judge_resume = params => get(`${baseQqUrl}query_user_judge_resume`,params);

//
export const query_judge_record = params => get(`${baseQqUrl}query_judge_record`,params);



